<template>
  <transition name="slide-right">
    <div class="detail-container" v-show="isReady&&visible" @click="handleClose">
      <div class="detail-box" @click.stop>
        <div class="detail-header">
          <div class="detail-title">{{picture.name}}</div>
          <div class="detail-close-button" @click="handleClose">
            <i class="detail-close-icon"></i>
          </div>
        </div>
        <div class="detail-body">
          <div class="detail-form">
            <div class="detail-item">
              <div class="detail-label">姓名</div>
              <div class="detail-control">
                {{picture.name}}
              </div>
            </div>
            <div class="detail-item">
              <div class="detail-label">性别</div>
              <div class="detail-control">{{Number(picture.sex)===1 ?'男':'女'}}</div>
            </div>
            <div class="detail-item">
              <div class="detail-label">星级评价</div>
              <div class="detail-control">
                <Rate class="rate-container" disabled allow-half :value="Number(picture.rank)/2"/>
              </div>
            </div>
            <div class="detail-item">
              <div class="detail-label">个人简介</div>
              <div class="detail-control">
                <Input class="show-textarea" :value="picture.introduction" type="textarea" :autosize="true" readonly/>
              </div>
            </div>
            <div class="detail-item">
              <div class="detail-label">训练介绍</div>
              <div class="detail-control">
                <Input class="show-textarea" :value="picture.trainContent" type="textarea" :autosize="true" readonly/>
              </div>
            </div>
            <div class="detail-item">
              <div class="detail-label">集赞数</div>
              <div class="detail-control vote-control">
                <span>{{picture.vote}}</span>
                <i class="vote-icon iconfont gym-icon-heart"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import { Rate } from 'iview'

export default {
  components: {Rate},
  props: {
    visible: [Boolean, Object]
  },
  watch: {
    'visible': function () {
      if (this.visible) {
        this.record = this.visible
      }
    }
  },
  computed: {
    picture: function () {
      return this.visible ? this.visible : this.record
    }
  },
  data () {
    return {
      isReady: false,
      record: {}
    }
  },
  mounted () {
    document.getElementsByClassName('layout-body')[0].appendChild(this.$el)
    this.isReady = true
  },
  destroyed () {
    this.$el.parentNode.removeChild(this.$el)
  },
  methods: {
    handleClose () {
      this.$emit('close')
    }
  }
}
</script>
<style lang='stylus' scoped>
  .detail-container
    position absolute
    top 0
    left 0
    z-index 10
    width 100%
    height 100%
    display flex
    align-items flex-end
    justify-content center
    flex-direction column
    padding 10px 0
  .detail-box
    width 360px
    flex 1
    background white
    display flex
    flex-direction column
    align-items stretch
    position relative
    box-shadow 0 2px 8px 0 rgba(0, 0, 0, 0.2)
  .detail-header
    border-bottom 1px solid #EAEAEA
    height 64px
    display flex
    flex-direction row
    align-items center
    padding-left 30px
  .detail-title
    flex 1
    font-size 16px
    color #333333
  .detail-close-button
    padding 10px
    padding-right 30px
    display flex
    justify-content center
    align-items center
    cursor pointer
  .detail-close-icon
    width 12px
    height 12px
    display inline-block
    background url('~@/assets/blue_close.png') no-repeat center
    background-size 100% 100%
  .detail-body
    flex 1
    overflow auto
  .detail-form
    font-size 14px
    padding-top 36px
  .detail-item
    margin-bottom 40px
    display flex
    flex-direction row
    align-items flex-start
  .detail-label
    color #333333
    width 90px
    display flex
    flex-direction column
    align-items flex-end
    justify-content center
  .detail-control
    padding-left 36px
    padding-right 32px
    color #666666
    flex 1
    overflow hidden
    word-break break-all
  .vote-control
    display flex
    flex-direction row
    align-items center
    justify-content flex-start
  .vote-icon
    color #FF7979
    font-size 24px
    margin-left 4px
  .rate-container
    font-size 28px
  .show-textarea /deep/ textarea
    resize none
    border none
    outline none
    box-shadow none
    padding 0
    color #666
</style>



