<template>
  <PageBody>
    <div class="list-container">
      <div class="query-body">
        <div>
          <Select style="width:200px;margin-right: 20px" :clearable="true"
                  :value="$route.query.sex" @on-change="handleSex">
            <Option value="1">男</Option>
            <Option value="0">女</Option>
          </Select>
          <Input size="default" prefix="ios-search" placeholder="昵称／个人简介/训练介绍关键字搜索" style="width: 250px"
                 :clearable="true"
                 :value="$route.query.keyword" @on-change="handleSearch"/>
        </div>
        <router-link tag="button" class="gym-btn plain-btn font-s" to='/wall/detail'>添加教练</router-link>
      </div>
      <div class="list-body" v-loading="isLoading">
        <p v-if="!listData.length" class="empty-list">
          <span v-if="$route.query.keyword">没有匹配信息</span>
          <span v-else>暂无数据</span>
        </p>
        <ul v-else class="list-ul">
          <li class="picture-box" v-for="(picture,i) in listData" :key="i"
              :class="{'active-picture':picture.id===coach.id}">
            <img :src="picture.image" alt="" class="picture-img" @click="handleZoom(picture)">
            <div class="picture-buttons top-buttons">
              <Tooltip content="编辑">
                <router-link tag="button" :to="'/wall/detail/'+picture.id">
                  <i class="iconfont gym-icon-edit"></i>
                </router-link>
              </Tooltip>
              <Tooltip content="删除">
                <button @click="handleDelete(picture)">
                  <i class="iconfont gym-icon-trash"></i>
                </button>
              </Tooltip>
            </div>
            <div class="picture-buttons bottom-buttons">
              <Tooltip content="放大" placement="top">
                <button @click="handleZoom(picture)">
                  <i class="iconfont gym-icon-zoom"></i>
                </button>
              </Tooltip>
              <Tooltip content="详情" placement="top">
                <button @click="handleMore(picture)">
                  <i class="iconfont gym-icon-more"></i>
                </button>
              </Tooltip>
            </div>
          </li>
        </ul>
      </div>
      <div class="paging-body">
        <Page
          :total="this.page.total"
          :current="this.page.pageNum"
          :page-size="this.page.pageSize"
          show-elevator show-total show-sizer :transfer="false"
          @on-change="pageNumChange"
          @on-page-size-change="pageSizeChange"
        />
      </div>
      <PictureDetail :visible="picture" @close="picture = false"/>
      <CoachDetail :visible="coach" @close="coach = false"/>
    </div>
  </PageBody>
</template>
<script>
import PictureList from 'views/components/PictureList'
import PictureDetail from './components/PictureDetail'
import CoachDetail from './components/CoachDetail'
import { getData, deleteDataById } from 'api/wall'
import list from '@/mixins/list'
import debounce from 'lodash/debounce'

export default {
  components: {PictureList, PictureDetail, CoachDetail},
  mixins: [list],
  data () {
    return {
      query: {},
      isLoading: false,
      listData: [],
      picture: false,
      coach: false,
      page: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  methods: {
    getList (data = {}) {
      this.isLoading = true
      let query = {
        pageSize: 10,
        pageNum: 1
      }
      this.query = Object.assign({}, query, data)
      getData(this.query).then(res => {
        if (Number(res.code) === 200) {
          this.listData = res.data
          res.pageNum && (this.page.pageNum = Number(res.pageNum))
          res.pageSize && (this.page.pageSize = Number(res.pageSize))
          res.total && (this.page.total = Number(res.total))
        } else {
          this.listData = []
          this.$message({
            message: res.message,
            type: 'warning'
          })
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    handleSearch: debounce(function (e) {
      this.listQuery({
        keyword: e.target.value,
        pageNum: 1
      })
    }, 1000),
    handleSex (value) {
      this.listQuery({
        sex: value,
        pageNum: 1
      })
    },
    pageNumChange (pageNum) {
      this.listQuery({
        pageNum: String(pageNum)
      })
    },
    pageSizeChange (pageSize) {
      this.listQuery({
        pageSize: String(pageSize)
      })
    },
    handleDelete (picture) {
      this.$confirm({
        title: '教练墙',
        brief: '确定删除此教练吗？',
        confirm: (dialog) => {
          dialog.loading = true
          deleteDataById({id: picture.id}).then(res => {
            if (Number(res.code) === 200) {
              dialog.destroy()
              this.listRefresh()
            } else {
              dialog.loading = false
              this.$message({
                message: res.message,
                type: 'warning'
              })
            }
          })
        }
      })
    },
    handleZoom (picture) {
      this.picture = picture
    },
    handleMore (picture) {
      this.coach = picture
    }
  }
}
</script>
<style lang='stylus' scoped>
  .list-container
    width 100%
    height 100%
    display flex
    flex-direction column
    align-items stretch
  .query-body
    padding 0 20px
    height 64px
    display flex
    flex-direction row
    align-items center
    justify-content space-between
    border-bottom 1px solid #EAEAEA
  .paging-body
    border-top 1px solid #EAEAEA
    height 60px
    width 100%
    padding-right 20px
    display: flex
    justify-content flex-end
    align-items center
  .list-body
    flex 1
    padding 0 20px
    overflow auto
  .list-ul
    display flex
    flex-wrap wrap
    margin-right -20px
    overflow hidden
    padding-top 20px
  .picture-box
    margin-right 30px
    margin-bottom 20px
    width 130px
    height 156px
    background #F4F4F4
    display flex
    position relative
    transition box-shadow 0.2s
    &.active-picture
      box-shadow 2px 2px 16px rgba(0, 0, 0, 0.6)
  .picture-img
    flex 1
  .picture-buttons
    position absolute
    left 0
    padding 0 6px
    width 100%
    display flex
    flex-direction row
    justify-content space-between
    &.top-buttons
      top 6px
    &.bottom-buttons
      bottom 6px
    & button
      background rgba(237, 237, 237, .4)
      color #524D4D
      width 20px
      height 20px
      border-radius 4px
    & .iconfont
      font-size 18px
      position relative
      top -3px
  .picture-describe
    padding 0 4px
    position absolute
    bottom 0
    width 100%
    height 24px
    line-height 24px
    background rgba(237, 237, 237, .4)
    color #2B3242
  .picture-buttons
  .picture-describe
    opacity 0
    transition opacity 0.2s
  .picture-box:hover .picture-buttons
  .picture-box:hover .picture-describe
    opacity 1
  .empty-list
    width 100%
    height 100%
    display flex
    align-items center
    justify-content center
</style>



