import request from 'utils/request'

// 取数据
export function getData (data) {
  return request({
    method: 'post',
    url: '/gym/coach/listCoachInfo',
    data
  })
}

export function addData (data) {
  return request({
    method: 'post',
    url: '/gym/coach/saveCoach',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function getDataById (data) {
  return request({
    method: 'post',
    url: '/gym/coach/getCoachById',
    data
  })
}

export function updateDataById (data) {
  return request({
    method: 'post',
    url: '/gym/coach/updateCoach',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function deleteDataById (data) {
  return request({
    method: 'post',
    url: '/gym/coach/delCoach',
    data
  })
}



